:root {
  --d: 2500ms;
  --angle: 90deg;
  --gradX: 100%;
  --gradY: 50%;
  --c1: rgb(23, 252, 240);
  --c2: rgba(168, 239, 255, 0.1);
}

.container {
  padding-top: 56px;
  width: 90%;
  margin: auto;
}

.container > div {
  padding-top: 44px;
}

.heading {
  font-size: 36px;
  font-weight: bold;
  padding: 0 15px;
  box-sizing: border-box;
}

.heading:hover {
  box-sizing: border-box;
}

.projectWrapper {
  width: 90%;
  margin: auto;
  padding: 5px;
  box-sizing: border-box;
  color: white;
}

@property --angle {
  syntax: "<angle>";
  initial-value: 90deg;
  inherits: true;
}

@property --gradX {
  syntax: "<percentage>";
  initial-value: 50%;
  inherits: true;
}

@property --gradY {
  syntax: "<percentage>";
  initial-value: 0%;
  inherits: true;
}

.wrapper {
  width: 90%;
  margin: auto;
  box-sizing: border-box;
  /* border: 1px solid yellowgreen; */
  margin-bottom: 64px;
}

.box {
  padding: 64px 20px;
  border: 1px solid gray;
  /* border: 0.35rem solid transparent; */
  border-top-right-radius: 12px;
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  background: rgb(20, 20, 20);
  background: linear-gradient(90deg, rgb(10, 10, 10,1) 54%, rgba(11,28,47,0.9920343137254902) 85%);
}


