@keyframes borderRotate {
  100% {
    --angle: 420deg;
  }
}

@keyframes borderRadial {
  20% {
    --gradX: 100%;
    --gradY: 50%;
  }
  40% {
    --gradX: 100%;
    --gradY: 100%;
  }
  60% {
    --gradX: 50%;
    --gradY: 100%;
  }
  80% {
    --gradX: 0%;
    --gradY: 50%;
  }
  100% {
    --gradX: 50%;
    --gradY: 0%;
  }
}

.descWrapper {
  width: 84%;
  margin: auto;
  padding: 0 16px 20px 16px;
  color: white;
  line-height: 2;
  font-size: 18px;
  border-radius: 8px;
  /* border: 1px solid goldenrod; */
}

img {
  width: 100%;
}
.monitor {
  width: 90%;
  height: 300px;
  overflow-y: scroll;
  border: solid 1em #444;
  border-radius: 0.5em;
  margin: auto;
  cursor: pointer;
}

.monitor::-webkit-scrollbar {
  width: 15px;
}
.monitor::-webkit-scrollbar-thumb {
  background: #666;
}
.monitor::-webkit-scrollbar-track {
  background-color: #888;
}

.projectHeading {
  font-size: 24px;
  font-weight: bold;
}

.techStackWrapper {
  display: flex;
  flex-direction: row;
}

.techStackTag {
  /* color: rgba(69, 232, 252, 1);
  background-color: black;
  border: 1px solid rgba(69, 232, 252, 1); */
  background-color: #050c14;
  border: 1px solid gray;
  border-radius: 8px;
  margin-right: 4px;
  font-size: 14px;
  font-weight: 700;
  width: 50%;
  padding: 2px 6px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.btnDiv {
  margin-top: 16px;
}

.btnStyle {
  border-radius: 6px;
  /* background: rgb(0, 0, 0);
  background: linear-gradient(
    90deg,
    rgba(0, 0, 0, 1) 36%,
    rgba(36, 36, 40, 1) 51%
    ); */
    /* border: 1px solid white; */
    background: rgb(0,0,0);
    background: linear-gradient(270deg, rgba(0,0,0,1) 54%, rgba(11,28,47,0.9920343137254902) 84%);

    border: 1px solid gray;
  color: white;
  text-align: center;
  font-size: 18px;
  padding: 4px 18px 4px 18px;
  width: 150px;
  transition: all 0.5s;
  cursor: pointer;
  margin: 5px;
}

.btnStyle span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
}

.btnStyle span:after {
  content: "\00bb";
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.btnStyle:hover span {
  padding-right: 25px;
}

.btnStyle:hover span:after {
  opacity: 1;
  right: 0;
}

.btnStyle:hover {
  background: black;
  border: 1px solid white;
}

h1 {
  color: white;
  font-family: "Space mono", monospace;
  font-size: 4rem;
}
.statsFlex {
  display: flex;
  justify-content: space-around;
  /* margin-bottom:24px; */
  margin-bottom: 36px;
}

.statsFlexElement {
  /* background-color:whitesmoke; */
  padding: 10px;
  border-radius: 10px;
  /* border:10px solid #3b3228; */
  width: 42%;
  margin: auto;
  height: 170px;
}

@media all and (max-width: 950px) {
  .statsFlex {
    display: flex;
    flex-direction: column;
    margin-bottom: 0;
  }

  .statsFlexElement {
    width: 70%;
    margin: auto;
    height: 180px;
  }

  .container {
    /* border: 1px solid red; */
    margin-top: 64px;
  }
}

@media all and (max-width: 430px) {
  .container {
    padding-top: 36px;
  }

  .statsFlex {
    display: flex;
    flex-direction: column;
  }

  .statsFlexElement {
    width: 70%;
    margin: auto;
    height: 90px;
  }
  .techStackWrapper {
    display: flex;
    flex-direction: row;
    /* border: 2px solid white; */
    /* width: 70%; */
    flex-wrap: wrap;
    gap: 10px;
  }
  .techStackTag {
    /* color: rgba(69, 232, 252, 1);
    background-color: black;
    border: 1px solid rgba(69, 232, 252, 1); */
    background-color: #050c14;
    border-radius: 4px;
    margin-right: 4px;
    /* padding: 2px 4px 2px 4px; */
    font-size: 10px;
    font-weight: bold;
    width: 40%;
    margin: auto;
  }

  .monitor {
    border: solid 0.5em #444;
    /* height: 50vh; */
    height: 300px;
    width:75%;
  }

  .monitor::-webkit-scrollbar {
    width: 8px;
  }

  .projectHeading {
    font-size: 16px;
  }

  .projectHeading + p {
    font-size: 14px;
    line-height: 1.5;
  }
  
  .wrapper{
    width: 100%;
  }
  
  .box {
    padding: 24px 12px;
    width: 100%;
  }
  
  

  .techStackPara {
    font-size: 12px;
  }

  .btnDiv {
    display: flex;
    /* flex-direction: column; */
    gap: 8px;
  }

  .btnStyle {
    font-size: 12px;
    padding: 2px 4px 2px 4px;
    width: 100px;
    margin: auto;
  }
}
